import { Block, Document } from '@ali/4ever-cangjie';
import isList from './isList';

/**
 * 寻找 listId 中首个列表项
 */
export default function findFirstListItemById(
  listItem: Block,
  document: Document,
) {
  if (!isList(listItem)) {
    return undefined;
  }
  const parent = document.getParent(listItem.key);
  return parent?.nodes.find((node) => {
    return (
      Block.isBlock(node) &&
      isList(node) &&
      node?.data?.list?.listId === listItem.data.list.listId
    );
  });
}
